import api from "../api";

const API_KEY = process.env.REACT_APP_API_KEY;
function getMovies() {


  return async (dispatch) => {
    try {
      dispatch({
        type: "GET_MOVIES_REQUEST"
      });
      
      const popularMovieAPI =  api.get(`movie/popular?api_key=${API_KEY}&language=en-US&page=1`);
      
      const topRatedAPI =  api.get(`movie/top_rated?api_key=${API_KEY}&language=en-US&page=1`);
      
      const upcomingAPI =  api.get(`movie/upcoming?api_key=${API_KEY}&language=en-US&page=1`);
      
      const genreAPI =  api.get(`genre/movie/list?api_key=${API_KEY}&language=en-US`)

      const movieDetailAPI =  api.get(`movie/popular?api_key=${API_KEY}&language=en-US&page=1`)
  
      let [popularMovies, topRatedMovies, upcomingMovies, genreList, movieDetailPage] =
      await Promise.all([
        popularMovieAPI,
        topRatedAPI,
        upcomingAPI,
        genreAPI,
        movieDetailAPI
      ]);
      
      // console.log(popularMovies);
      // console.log(topRatedMovies);
      // console.log(upcomingMovies);
      console.log("",genreList);
      console.log("",movieDetailPage)
  
      dispatch({
        type: "GET_MOVIES_SUCCESS",
        payload: {
          popularMovies: popularMovies.data,
          topRatedMovies: topRatedMovies.data,
          upcomingMovies: upcomingMovies.data,
          genreList: genreList.data.genres,
          movieDetailPage: movieDetailPage.data,
        }
      })

    } catch(error) {
      //  
      dispatch({
        type: "GET_MOVIES_FAILURE"
      })
    }
  };  
}

export const movieAction = {
  getMovies,
};