import React from 'react'

const Banner = ({movies}) => {
  console.log(movies)
  return (
    <div className='banner' style={{backgroundImage: `URL(`+`https://www.themoviedb.org/t/p/w1920_and_h800_multi_faces${movies.poster_path}`+`)`,}}>
      <div className='banner-info'>
        <h1>{movies.title}</h1>
        <p>{movies.overview}</p>
      </div>
    </div>
    
  )
}

export default Banner
