import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { useState } from 'react';

const Navigation = ({authenticate, setAuthenticate}) => {
  const navigate = useNavigate();
  const [keyword, setKeyword] = useState("");
  const handleOnKeyword = (e) => {
    setKeyword(e.target.value)
  }

  return (
    <Navbar bg='dark' variant='dark'>
    <Container fluid>
      <Navbar.Brand>
        <img width={100} src='https://upload.wikimedia.org/wikipedia/commons/7/7a/Logonetflix.png'/>
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="navbarScroll" />
      <Navbar.Collapse id="navbarScroll">
        <Nav
          className="me-auto my-2 my-lg-0"
          style={{ maxHeight: '100px' }}
          navbarScroll>          
          <Link to='/home' className='nav-item' href="#action1">Home</Link>
          <Link to='/movie' className='nav-item'  href="#action2">Movies</Link>
          <div className='login-btn'>
            {authenticate ? (
                <div onClick={() => setAuthenticate(false)}>Logout</div>
              ) : (
                <div onClick={() =>navigate("/")}>Login</div>
              )
            } 
          </div>
        </Nav>
        {authenticate && <Form className="d-flex">
          <Form.Control
            value={keyword}
            onChange={handleOnKeyword}
            type="search"
            placeholder="Search"
            className="me-2"
            aria-label="Search"
          />
          <Button variant="outline-danger">Search</Button>
        </Form>}
      </Navbar.Collapse>
    </Container>
  </Navbar>
  )
}

export default Navigation
