import React from 'react'
import { Badge } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { Link } from 'react-router-dom'

const MovieCard = ({item}) => {
  
  // const navigate = useNavigate();
  // const handleClick = () => {
  //   navigate(`/movies/${item.id}`, {
  //     state: {value: {item}, genreContents: {genreList}},
  //   });
  // }

  const {genreList} = useSelector((state) => state.movie);  
  const {
    title,
    id,
    backdrop_path,
    poster_path,
    genre_ids,
    release_date,
    overview,
    adult,
    name,
    vote_average
  } = item;
  return (
    <>
    <Link
    to={`/movies/${item.id}`}
    state={{
      title:title,
      id:id,
      backdrop_path:backdrop_path,
      poster_path:poster_path,
      genre_ids: genre_ids,
      release_date: release_date,
      overview: overview,
      adult: adult,
      name: name,
      vote_average:vote_average,
    }}
    >
    <div
    // onClick={handleClick}
    className='card'
    style={{backgroundImage: 'url('+`https://www.themoviedb.org/t/p/w355_and_h200_multi_faces${item.poster_path}`+')'}}>
      <div className='overlay'>
        <h1>{item.title}</h1>
        <div>
          {item.genre_ids.map((id) => (
            <Badge bg="danger">{genreList.find((item) => item.id === id).name}</Badge>
          ))}
        </div>
        <div>
          <span>{item.vote_average}</span>
          <span>{item.adult ? "" : "Under18"}</span>
        </div>
      </div>
    </div>
    </Link>
    </>
  )
}
export default MovieCard
