import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Badge } from 'react-bootstrap'
import { ClipLoader } from 'react-spinners'
import { useLocation } from 'react-router-dom';
import { CiCirclePlus } from "react-icons/ci";
import { IoMdArrowDropright
} from "react-icons/io";
import { CiVolumeHigh, CiVolumeMute} from "react-icons/ci";
import { IoMdHeartEmpty,IoMdHeart  } from "react-icons/io";

const MovieDetail = ({state}) => {
  /////////////////////////////////

  const {genreList} = useSelector((state) => state.movie);

  const location = useLocation();
  console.log(location)

  const [mute, setMute] = useState(false);
  const onClickMute = () => {
    setMute((e) => !e)
  }
  const [heart, setHeart] = useState(false);
  const onClickHeart = () => {
    setHeart((e) => !e)
  }

  ///////////////////////////////
  //  find   if 
  // const movieItem = useLocation();
  // const movieInfo = movieItem;

  // if(loading) {
  //   return (
  //     <div className='loader'>
  //     <ClipLoader
  //     color='#fff'
  //     loading={loading}
  //     size={150}
  //   />
  // </div>
  //   )
  // }
  
  return (
    <div className='movie-detail'>
    <div className='movie-detail-img'
    style={{backgroundImage: `url(`+`https://www.themoviedb.org/t/p/w1920_and_h800_multi_faces${location.state.backdrop_path}`+`)`,}}
    >
       <div className='background-grdiant'></div>
    <h1>{location.state.title}</h1>
    <div className='mute'>
          {mute ? <CiVolumeMute onClick={onClickMute}/> : <CiVolumeHigh onClick={onClickMute}/>}
        </div>
    </div>
      <div className='detail'>
        <div className='btn-box'>
          <button>
          <IoMdArrowDropright className='play-btn'/>
            </button>
          <CiCirclePlus/>
          {heart ? <IoMdHeart className='heart' onClick={onClickHeart}/> : <IoMdHeartEmpty className='heart' onClick={onClickHeart}/>}
        </div>
        {/* <div>
        {location.state.genre_ids.map((id) => (
            <Badge bg="danger">
            {genreList.find((item) => item.id === id).name}
            </Badge>
          ))}
        </div> */}
        <div className='info'>
        <p> : {location.state.release_date}</p>
        <p>{location.state.adult ? "" : "18   "}</p>
        <p> : {location.state.vote_average}</p>
      </div>
      <p>{location.state.overview}</p>
      <div className='video'><h2>Video :)</h2></div>
      </div>
    </div>
  )
}

export default MovieDetail
